@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap");

.home {
    background-image: url("../media/homeBackground.jpeg");
    background-size: cover;
    width: 100vw;
    height: 100vh;
    font-family: "Noto Sans KR", sans-serif;
}

.home h1 {
    display: flex;
    align-content: center;
    justify-content: center;
    padding-top: 10%;
    font-size: 1000%;
    /* color: sandybrown; */
    color: #455;
}

.home h2 {
    color: white;
    display: flex;
    align-content: center;
    justify-content: center;
    letter-spacing: 12px;
}

@media screen and (max-width: 425px) {
    .home {
        width : 100vw;
       
    }

    .home h1 {
      font-size: 600%;;
    }

    .home h2 {
        text-align: center;
    }
 }
